body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  font-family: "Open Sans", sans-serif;
  color: #333;
  background-color: #faf8f6;
}

.hero-is-fullheight {
  min-height: 100vh;
}
.hero-head {
  flex-grow: 0;
  flex-shrink: 0;
}
.hero-body {
  align-items: center;
  display: flex;
}
.hero-foot {
  flex-grow: 0;
  flex-shrink: 0;
}

.heading-title {
  font-family: "Open Sans", sans-serif;
  color: #333;
  font-size: 2.25rem;
  line-height: 1.07;
  letter-spacing: -0.5px;
}
.heading-subtitle {
  padding: 0 1rem;
  font-size: 1.75rem;
}

.image-128x128 {
  width: 212px;
  height: 212px;
  margin-left: auto;
  margin-right: auto;
  display: inline-block;
  position: relative;
  margin-right: 0.5rem;
  margin-bottom: 0.5rem;
}

@media screen and (max-width: 992px) {
  .image-128x128 {
    width: 150px;
    height: 150px;
    margin-right: 0.25rem;
    margin-bottom: 0.25rem;
  }
}

.image-212 {
  width: 212px;
  height: 159px;
  margin-left: auto;
  display: inline-block;
  position: relative;
  margin-right: 0.5rem;
  margin-bottom: 0.5rem;
}

@media screen and (max-width: 992px) {
  .image-212 {
    width: 128px;
    height: 96px;
    margin-right: 0.25rem;
    margin-bottom: 0.25rem;
  }
}

.image-75 {
  width: 75%;
  height: auto;
  margin-left: auto;
  margin-right: auto;
  display: block;
  position: relative;
}

.image-50 {
  width: 50%;
  height: auto;
  margin-left: auto;
  margin-right: auto;
  display: block;
  position: relative;
}
.image-rounded {
  border-radius: 0.4em 0.4em 0.4em 0.4em;
}
.centered-and-cropped {
  object-fit: cover;
}

.text-center {
  text-align: center !important;
}

.btn-rounded {
  border-radius: 290486px;
  padding-left: 1em;
  padding-right: 1em;
}

.btn-outline-dark {
  background-color: #fff;
  border-color: #dbdbdb;
  border-width: 1px;
  color: #363636;
  cursor: pointer;
  justify-content: center;
  padding-bottom: calc(0.375em - 1px);
  padding-left: 0.75em;
  padding-right: 0.75em;
  padding-top: calc(0.375em - 1px);
  text-align: center;
  white-space: nowrap;
}

.restaurant-title {
  font-family: "Noto Serif SC", serif;
  font-weight: 900;
  font-size: 2.5rem;
}

.heading-title {
  font-family: "Noto Serif SC", serif;
  font-weight: 900;
  font-size: 2rem;
  padding: 0.5rem;
}
.heading-subtitle {
  font-family: "Open Sans", sans-serif;
  padding: 0.1rem;
  font-size: 1.25rem;
}

@media screen and (max-width: 992px) {
  .nav-collapese_mobile {
    background-color: #faf8f6;
  }
  .nav-item__mobile {
    font-family: "Open Sans", sans-serif;
    color: #333;
    background-color: #faf8f6;
    border-top: 1px solid #666;
    padding-left: 10%;
    padding-right: 10%;
  }
}

.p-article {
  font-family: "Open Sans", sans-serif;
  font-size: 1rem;
  padding: 0.5rem 0.75rem;
  font-size: 0.875rem;
  margin: 0 0 16px;
  padding-left: 5%;
  padding-right: 5%;
}
.p-caption {
  font-family: "Open Sans", sans-serif;
  margin-top: -1.25rem;
  font-size: 0.8125rem;
  color: #4a4a4a;
  font-weight: 400;
  line-height: 1.25;
  word-break: break-word;
  text-align: center;
  padding-left: 5%;
  padding-right: 5%;
}

.restaurant-wrapper {
  margin-top: 30px;
  margin-bottom: 30px;
}

@media screen and (min-width: 992px) {
  .restaurant-title {
    padding-right: 0.5rem;
    padding-left: 0.5rem;
  }
}

.notFound__link {
  font-family: "Open Sans", sans-serif;
  color: #333;
  font-size: 0.875rem;
  line-height: 1.25;
  letter-spacing: 0.5px;
  text-align: center;
  text-decoration: none;
}

.notFound__link:hover {
  color: #b24f83;
  text-decoration: none;
}

.has-background-clear {
  background-color: transparent;
}
.has-background-gradients {
  background: #b24f83; /* fallback for old browsers */ /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to right,
    #9e1f62,
    #b24f83
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.sub-header {
  height: 31px;
  padding-bottom: 0px;
  padding-top: 0px;
  padding-left: 0px;
  padding-right: 0px;
}

@media screen and (max-width: 992px) {
  .sub-header {
    height: 62px;
    padding-bottom: 10px;
    padding-top: 10px;
    padding-left: 10%;
    padding-right: 10%;
  }
}
.sub-header__text {
  color: hsl(0, 0%, 98%);
  cursor: pointer;
  background-color: transparent;
  display: flex;
  align-items: center;
  flex-grow: 0;
  flex-shrink: 0;
  padding: 0.5rem 0.75rem;
  position: relative;
  font-size: 0.8125rem;
  line-height: 1.785;
  color: #fff;
  font-style: normal;
  font-weight: 400;
  letter-spacing: normal;
  font-stretch: normal;
  font-family: "Open Sans", sans-serif;
}

@media screen and (max-width: 992px) {
  .sub-header__text {
    font-size: 01rem;
  }
}

.sub_header__link {
  color: #fff;
  text-decoration: none;
  transition: color 0.3s cubic-bezier(0.39, 0.01, 0.03, 0.99);
}

.sub_header__link:hover {
  color: #f0ede8;
  text-decoration: none;
}

.promo-bar {
  padding: 4px 12px;
  font-size: 0.8125rem;
}

.image-250 {
  margin-left: auto;
  margin-right: auto;
  width: 250px;
  height: auto;
}
.image-300 {
  margin-left: auto;
  margin-right: auto;
  width: 300px;
  height: auto;
}
.image-350 {
  margin-left: auto;
  margin-right: auto;
  width: 350px;
  height: auto;
}

.col-centered {
  float: none;
  margin: 0 auto;
}

.card-content {
  padding: 1.5rem;
  background-color: #fff;
  box-shadow: 0 2px 3px rgba(10, 10, 10, 0.1), 0 0 0 1px rgba(10, 10, 10, 0.1);
  color: #4a4a4a;
  max-width: 96%;
  position: relative;

  margin-left: auto;
  margin-right: auto;
}

.card-image {
  display: block;
  position: relative;
}

.text-footer,
a.text-footer {
  color: hsl(0, 0%, 98%);
}

a.text-footer:hover {
  color: #b24f83;
}

.text-center {
  text-align: center !important;
}

.footer {
  padding: 3rem 1.5rem 3rem;
}

.site-logo {
  max-width: 225px;
  border-radius: 0.4em 0.4em 0.4em 0.4em;
}

.list-group-item {
  position: relative;
  padding: 0.25rem 1rem;
  margin-bottom: -1px;
  background-color: transparent;
  border: 0;
}

.footer__link {
  color: #faf8f6;
  transition: color 0.3s cubic-bezier(0.39, 0.01, 0.03, 0.99);
  text-decoration: none;
}

.footer__link:hover {
  color: #f0ede8;
  transition: color 0.3s cubic-bezier(0.39, 0.01, 0.03, 0.99);
  text-decoration: none;
}

.contact__link {
  color: #333;
  text-decoration: none;
  transition: color 0.3s cubic-bezier(0.39, 0.01, 0.03, 0.99);
}

.contact__link:hover {
  color: #b24f83;
  text-decoration: none;
}

@media screen and (max-width: 992px) {
  #google-maps-lg {
    display: none;
  }
  #google-maps-sm {
    display: visible;
  }
}
@media screen and (min-width: 993px) {
  #google-maps-lg {
    display: visible;
  }

  #google-maps-sm {
    display: none;
  }
}

#site-header {
  position: relative;
  z-index: 200;
}

.navbar {
  position: static;
}

.navbar .nav > li {
  position: relative;
  z-index: 100;
}
.main-menu {
  height: 54px;
  background-color: #faf8f6;
}

.dropdown-menu {
  background-color: #faf8f6;
}

.header-link {
  color: #000;
  background-color: #faf8f6;
  transition: color 0.3s cubic-bezier(0.39, 0.01, 0.03, 0.99);
  transition-property: color;
  transition-duration: 0.3s;
  transition-timing-function: cubic-bezier(0.39, 0.01, 0.03, 0.99);
  transition-delay: 0s;
  font-weight: 400;
  padding: 19px 12px;
  line-height: 1;
  border-bottom: 0;
  border-bottom-width: 0px;
  border-bottom-style: none;
  border-bottom-style: initial;
  border-bottom-color: currentColor;
  border-bottom-color: initial;
  white-space: nowrap;
}

.header-link:hover {
  background-color: #f0ede8;
  text-decoration: none;
}

@media screen and (max-width: 992px) {
  .header-link {
    overflow-x: visible;
    overflow-y: auto;
    display: block;
    border-bottom: 1px solid #f0ede8;
  }
  .navbar-nav .nav-link {
    padding-left: 3%;
  }
}

/* Header Parallax Element Style*/
.paral {
  height: 400px;
  background-attachment: fixed;
  background-size: cover;
  background-position: 50% 50%;
}

@media screen and (max-width: 992px) {
  .paral {
    display: none;
  }
  .paral__text {
    display: none;
  }
  .image-h-250 {
    width: 90%;
    height: auto;
    display: block;
    margin: 0 auto;
  }
  .paral-wrapper {
    margin-top: 1rem;
    margin-bottom: 1rem;
  }
}
@media screen and (min-width: 992px) {
  .image-h-250 {
    display: none;
  }
  .paral-wrapper {
    margin-top: 100px;
    margin-bottom: 100px;
  }
}

/* Paragraph for Parallax Section */
.paral p {
  font-size: 24px;
  color: #f5f5f5;
  text-align: center;
  line-height: 60px;
}

/* Heading for Parallax Section */
.paral h1 {
  color: rgba(255, 255, 255, 0.8);
  font-size: 60px;
  text-align: center;
  padding-top: 60px;
  line-height: 100px;
}

/* Image for Parallax Section */
.paralsec {
  background-image: url("https://i.imgur.com/HVDB9dR.jpg");
}

.paralsec1 {
  background-image: url("https://i.imgur.com/KtymcFG.jpg");
}

.paralsec2 {
  background-image: url("https://i.imgur.com/M6TvbsT.jpg");
}

